.pogoSlider-dir-btn:focus,
.pogoSlider-nav-btn:focus {
  outline: 0;
}

.pogoSlider {
  width: 100%;
  height: 0;
  padding-bottom: 38% !important;
  position: relative;
  overflow: hidden;
}

#column-left .pogoSlider,
#column-right .pogoSlider,
#content .pogoSlider {
  margin-bottom: 20px;
}

