.bannerH {
  width: 100%;
  height: 100%;
  position: absolute;
  background-size: cover;
}
.iconFunction {
  box-shadow: 0px 1px 36px 0px #031d35;
  width: 100%;
  float: left;
  text-align: center;
  color: #000;
  margin: 25px 0px 0px;
  background: rgb(255 255 255 / 88%);
  padding: 10px;
  border-radius: 5px;
  min-height: 135px;
}
.iconFunction.topbg {
  /* background-image: url("../images/career.jpg"); */
  background-size: auto 100%;
  position: relative;
}
.iconFunction.topbg2 {
  /* background-image: url("../images/counsel.jpg"); */
  background-size: auto 100%;
  position: relative;
}
.iconFunction.topbg3 {
  /* background-image: url("/public/assets/images/potential.jpg"); */
  background-size: auto 100%;
  position: relative;
}
.iconFunction.topbg a,
.iconFunction.topbg2 a,
.iconFunction.topbg3 a {
  background: rgb(255 255 255 / 70%);
  position: absolute;
  left: 0;
  bottom: 39px;
}
.iconFunction a {
  width: 100%;
  float: left;
  text-align: center;
  color: #000;
}
.iconFunction a i {
  width: 100%;
  float: left;
  text-align: center;
  color: #000;
  font-size: 40px;
}
.iconFunction a img {
  height: 40px;
